const hiddenBlock = {
  title: "Designing is The CooL LOVE",
  body:
    "Our most popular service is our Virtual Receptionist. We know that sometimes it’s something and you middle most this job was posted less than five minutes ago, and we think it’s a good match for you. If you submit a proposal now, you’ll be one of the first candidates."
};

const btnOpen = document.querySelector(".b-card__btn");
const cont = document.querySelector(".b-card__cont");

cont.addEventListener("click", function() {
  let target = event.target;
  const content = document.querySelector(".b-card__cont");
  const parentBtn = target.parentElement;
  const heading = document.querySelector(".b-card__heading");
  const text = document.querySelector(".b-card__text");
  const button = document.querySelector(".b-card__btn");
  if (target.classList.contains("b-card__btn")) {
    parentBtn.classList.add("b-card-hov");
    heading.parentNode.removeChild(heading);
    text.parentNode.removeChild(text);
    button.parentNode.removeChild(button);
    parentBtn.classList.remove(
      "b-card",
      "b-card--bg1",
      "b-card--bg2",
      "b-card--bg3",
      "b-card--bg4",
      "b-card--bg5",
      "b-card--bg6"
    );
    heading.classList.remove("b-card__heading");
    // function createBlock({ title, body } = {}) {
    const hover = document.querySelector(".b-card__hover");

    const h2 = document.createElement("h2");
    h2.textContent = hiddenBlock.title;
    h2.classList.add("b-card__h2");

    const h4 = document.createElement("h4");
    h4.textContent = hiddenBlock.body;
    h4.classList.add("b-card__h4");

    const buttonCross = document.createElement("button");
    buttonCross.textContent = "Close";
    buttonCross.classList.add("b-card__cross");

    parentBtn.appendChild(h2);
    parentBtn.appendChild(h4);
    parentBtn.appendChild(buttonCross);

    //   return hover;
    // }
  }
  // const cardHov = document.querySelector(".b-card--bg1-hov");
  const hover = document.querySelector(".b-card__hover");
});

